<template>
	<div id="app">
			<button @click="showIsShow= !showIsShow" class="btn btn-danger">show 显示/隐藏{{showIsShow}}</button>	
		    <div class="box" v-show="showIsShow">
				我是切换的div show
			</div>
			<br>
				<button @click="ifIsShow= !ifIsShow" class="btn btn-success">if 显示/隐藏{{ifIsShow}}</button>
			<div class="box" v-if="ifIsShow">
				我是切换的div if
			</div>
			
		</div>
</template>

<script>
	export default {
  name: 'jgwlj',
  data () {
  	return {
  		ifIsShow: true,
		  		showIsShow: true,
  	}
   
    }
  }
</script>

<style>
	.box{
	    		width: 300px;
	    		height: 300px;
	    		background: pink;
	    	}
</style>


